<template>
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/societyBack" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>反馈管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="'info1-'+index">
      <div class="chairman">
        <div class="list" v-for="(s,index) in item.lists" :key="index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
        <div style="width:100%; background-color:white;" v-for="(s,index) in item.wenzi" :key="'info2-'+index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
          <a>
            {{s.zi}}
          </a>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          lists: [
            {
              p: "活动主题",
              span: "助老助残"
            },
            {
              p: "反馈人",
              span: "张磊"
            },
            {
              p: "性别",
              span: "男"
            },
            {
              p: "联系方式",
              span: "15897230294"
            },
            {
              p: "发布机构",
              span: "雨滴筹"
            },
            {
              p: "参加人数",
              span: "15人"
            },
            {
              p: "活动日期",
              span: "2019-02-06"
            },
            {
              p: "反馈日期",
              span: "2019-02-25"
            },
            {
              p: "活动地点",
              span: "西宁市体育场"
            }
          ],
          wenzi:[
              {
                  p:'反馈意见',
                  span:'',
                  zi:'通过本次活动，我明白了做一个有社会责任感的人，应当成为每个人的共同价值追求和人生理想。虽然，我们中的大多数人平凡而卑微，但这并不妨碍我们做一个有社会责任感的人。多一份热情，主动勇敢，多一些对社会的关注和对他人的关爱。当别人遇到困难和不幸时，能够毫不犹豫的伸出援助之手；当面对不良现象时，能够大胆的发出质疑和批评的声音；当面对邪恶和危险时，能够勇敢挺身而出....只有这样，我们的人生才会变得更有价值和意义，我们生活的这个也将因人性美的彰显而变得更加美好。'
              }
          ]
        }
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left: 32%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman .list {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
main a {
  font-size: 0.87rem;
  line-height: 1.3rem;
  text-indent: 2rem;
  letter-spacing: 0.05rem;
  text-align: left;
  width: 95%;
  margin: 0 auto;
  color: #313131;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
</style>


